//      .
// type      'LOGIN_SUCCESS' .
// payload      .
export const loginSuccess = (token, role) => {
    return {
        type: 'LOGIN_SUCCESS',
        payload: { token, role }
    };
};



//     .
// type      'LOGOUT' .
export const logout = () => {
    //window.localStorage.removeItem('auth_token');
    return {
        type: 'LOGOUT'
    };
};

